import { Component, ViewChild } from '@angular/core';
import { Platform, Nav } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { PostagensPage } from '../pages/postagens/postagens';
import { MensagensPage } from '../pages/mensagens/mensagens';


import { ContatosPage } from '../pages/contatos/contatos';



@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) navCtrl: Nav;
    rootPage:any = ContatosPage;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });
  }
  goToContatos(params){
    if (!params) params = {};
    this.navCtrl.setRoot(ContatosPage);
  }goToPostagens(params){
    if (!params) params = {};
    this.navCtrl.setRoot(PostagensPage);
  }goToMensagens(params){
    if (!params) params = {};
    this.navCtrl.setRoot(MensagensPage);
  }
}



